<template>
  <div class="out">
    <div class="flip-card full" v-for="(player, i) in playersSorted" :key="player.name">
      <div class="flip-card-inner">
        <div class="flip-card-front" :style="'background-color:' + player.color + ';color:' + player.color + ';'">
          <h1 class="unselectable">{{ i + 1 }}.</h1>
        </div>
        <div class="flip-card-back" :style="'background-color:' + player.color + ';color:' + player.color + ';'">
          <h2>{{ player.name }}</h2>
          <p class="unselectable" style="font-size:large;" @dblclick="changeText($event, player)" @mousedown="startPress(player)" @mouseup="stopPress">
            {{ player.text }}
          </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Players",
  props: {
    players_prop: Array,
  },
  data() {
    return {
      players: this.players_prop,
      timeout: null,
      pressDuration: 0,
    };
  },
  methods: {
    changeText(e, player) {
      e.preventDefault();
      this.$parent.setShowModal(player);
    },
    startPress(player) {
      console.log("start press");
      this.timeout = setTimeout(() => {
        this.$parent.setShowModal(player);
      }, 2000);
    },
    stopPress() {
      clearTimeout(this.timeout);
    },
  },
  computed: {
    playersSorted() {
      return this.players_prop.slice().sort((p1, p2) => p2.sum - p1.sum);
    },
  },
};
</script>
