<template>
  <transition name="modal">
    <div class="modal-mask">
      <div class="modal-wrapper">
        <div class="modal-container">
          <form action="submit" @submit="onSubmit">
            <div v-if="player">
              <div class="modal-header">
                <h2 :style="'color:' + player.color + ';margin-top:0.2em;margin-bottom:1em;'">{{ player.name }}'s Spruch</h2>
              </div>
              <div class="flex-container">
                <textarea style="width:85%;min-height:2rem;padding-top:1rem;padding-bottom:1rem" class="input" v-model="player.text" placeholder="Spruch..." />
                <input style="margin-bottom:1em;width:85%;" class="input" type="password" v-model="secret" placeholder="Geheimcode" />
              </div>
              <div class="flex-container">
                <button class="button" @click="$emit('close')">
                  X
                </button>
                <button class="button save-button" type="submit">
                  Speichern
                </button>
              </div>
            </div>
          </form>
        </div>
      </div>
    </div>
  </transition>
</template>
<script>
import axios from "axios";

export default {
  props: {
    player_prop: Object,
  },
  data() {
    return {
      player: Object.assign(this.player_prop),
      secret: "",
    };
  },
  methods: {
    onSubmit(e) {
      e.preventDefault();

      axios
        // .post(`https://n64-stars.herokuapp.com/api/player/${this.player.id}`, {
        .post(`http://localhost:4000/api/player/${this.player.id}`, {
          text: this.player.text,
          secret: this.secret,
        })
        .then((res) => {
          if (res.status !== 200) alert("something went wrong");
          this.$parent.updatedPlayer(this.player.id, this.player.text);
        })
        .catch((error) => {
          let errMsg;
          if (error.response) {
            if (error.response.status === 401) errMsg = "Falsches Password. Ãœberlege nochmals :)";
            else errMsg = error.response.data;
          } else errMsg = error;
          alert(errMsg);
        });
    },
  },
  // created() {
  //   console.log("created");
  //   console.log(this.player_prop);
  // },
  // mounted() {
  //   console.log("mounted");
  //   console.log(this.player_prop);
  // },
};
</script>
