<template>
  <form>
    <div class="flex-container">
      <button class="button nrbutton pulsate" @click="$emit('abort')">Abbrechen</button>
      <button class="button save-button pulsate" @click="onSubmit">Speichern</button>
      <div class="p-container" v-for="player in players" :key="player.name">
        <label class="label" :for="player.name">{{ player.name }}</label>
        <input
          class="input"
          style="max-width:50px;color: red;background-color: #ffcccc;border-color: #ff3333;"
          placeholder="..."
          :name="player.name"
          :id="player.name"
          type="number"
          v-model="player.newPoints"
        />
      </div>
    </div>
  </form>
</template>

<script>
export default {
  name: "NewResultForm",
  props: {
    players: Array,
  },
  methods: {
    onSubmit(evt) {
      evt.preventDefault();
      const pointsPerPlayer = [];

      this.players.forEach((p) => {
        pointsPerPlayer.push({
          playerId: p._id,
          newPoints: p.newPoints || 0,
        });
      });
      this.$emit("close", pointsPerPlayer);
    },
  },
};
</script>
